import React from "react"
import { useNavigate } from "react-router-dom"
import "../../stylesheets/Dashboard.css"

function DashboardNavbar() {
  const navigate = useNavigate()
  return (
    <div className="dashboard-bar-container">
      <p>
        <button
          class="btn btn-primary"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#collapseWidthExample"
          aria-expanded="false"
          aria-controls="collapseWidthExample"
        >
          <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" class="bi bi-list" viewBox="0 0 16 16">
            <path
              fill-rule="evenodd"
              d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"
            />
          </svg>
        </button>
      </p>
      <div>
        <div class="collapse collapse-vertical" id="collapseWidthExample">
          <div class="card card-body dashboard-bar-card">
            <div className="dashboard-bar">
              <button className="btn btn-primary" onClick={() => navigate("/dashboard")}>
                Dashboard
              </button>
              <button className="btn btn-primary" onClick={() => navigate("/dashboard/products")}>
                Products
              </button>
              <button className="btn btn-primary" onClick={() => navigate("/dashboard/users")}>
                Users
              </button>
              <button className="btn btn-primary" onClick={() => navigate("/dashboard/orders")}>
                Orders
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default DashboardNavbar
